@import url('https://fonts.googleapis.com/css2?family=Quicksand:wght@300;400;500;600;700&display=swap');


/* base styles */
* {
  margin: 0;
  font-family: "Quicksand";
  color: #333;
}

/* !nav bar start */
.navbar {
  padding: 20px;
  display: flex;
  align-items: center;

  max-width: 800px;
  margin: 0 auto;
  border-bottom: 1px solid #f2f2f2;
}



.navbar h1 {
  color: #f1356d;
}

.navbar .links {
  margin-left: auto;
}



.navbar a {
  font-size: large;
  margin-left: 16px;
  text-decoration: none;
  padding: 6px;
}

.links :hover {

  border-bottom: 5px solid lightgoldenrodyellow;
  border-width: 3px;


}


.navbar .active {
  box-shadow: 1px 1px 1px lightblue;
  background: lightpink;
  border-radius: 5px;
  font-weight: bold;
  color: #f1356d;

}

/* ! nav bar end */


.home {

  margin-top: 20px;
  flex: auto;
  flex-direction: column;
  align-items: center;
  justify-content: center;

}

/*! Blogs stuff  */


.blog-preview {
  text-decoration: none;
  padding: 10px 16px;
  margin: 20px 0;
  border-bottom: 1px solid #a19c9c;




}

.blog-preview:hover {
  box-shadow: 1px 3px 5px black;
  border-left: 2px solid red;
}

.blog-preview h2,
a {

  font-size: 20px;
  color: #f1356d;
  margin-bottom: 8px;
  text-decoration: none;
}

.blog-preview button {

  font-size: medium;
  color: #f13591;
  background: whitesmoke;
  border: 1px white;
}


.blog-preview button:hover {

  font-size: medium;
  color: #f13591;
  background: pink;
  border: 3px solid grey;
}

.blog-details {
  max-width: 600px;

  margin: 40px auto;
  padding: 20px;
  padding-top: 20px;
}

.blog-details article {

  padding-top: 15px;

  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: x-large;

}

.blog-details article>h2 {
  border-bottom: 2px solid;
  margin-left: 0px;
  padding-left: 15px;
  padding-bottom: 10px;
  font-size: xx-large;
  align-self: self-start;

  width: 100%;
  color: #f13591;
}

.blog-details article>p {
  margin-left: 0px;
  padding-left: 15px;
  font-size: xx-large;
  align-self: self-start;
  width: 100%;
  margin-top: 30%;
  color: #f23765;
  border-left: 3px dotted;


}



.blog-details button {


  border: #f13591 solid;
  color: white;
  border-radius: 20px;
  background: #f1356d;
  font-weight: 500;
  font-size: large;

  margin-top: 5%;
  cursor: pointer;
}



/* !blog stuff end */




/* ? contacts page */
.content {
  max-width: 600px;

  margin: 40px auto;
  padding: 20px;
  padding-top: 20px;
}

.contacts {
  display: flex;
  align-items: center;
  align-content: center;
  flex-direction: column;

  position: absolute;
  top: 50%;
  left: 50%;
  margin-right: -50%;
  transform: translate(-50%, -50%);


}

.contacts p {

  position: relative;
  margin-left: 0;
}

/* ? contacts page end */


/* Error page  */
.Error-page ,.err{


  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;

}

.Error-page img {
  width: 150%;
  height: 150%;


}




/* error page end */


/* todo: Create Blog page  */

.create {
  padding: 10px;
  display: flex;
  flex-direction: column;
  justify-content: start;
  max-width: 800px;
  margin: auto;
  text-align: center;


}

.create label {
  display: block;
  text-align: left;
  font-weight: bold;

}

.create h2 {
  display: block;
  text-align: left;
  font-size: 20px;
  color: #f1356d;
  margin-bottom: 30px;
}

.create input,
.create textarea,
.create select {
  width: 100%;
  padding: 6px 10px;
  margin: 10px 0px;
  border: 1px solid #ddd;
  box-sizing: border-box;
  display: block;
}

.create button {
  background: #f1356d;
  color: #fff;
  border: 0;
  padding: 8px;
  border-radius: 8px;
  cursor: pointer;
}

/* todo: Create Blog page end  */



.loader {
  border: 16px solid #f3f3f3;
  /* Light grey */
  border-top: 16px solid #f1356d;
  /* Blue */
  border-radius: 50%;
  width: 120px;
  height: 120px;
  animation: spin 2s linear infinite;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}